@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{background-color:#333;padding:0 20px;display:flex;align-items:center;margin:0 auto}.navbar h1{font-size:large;color:white}

.button{background:#fff;border-radius:6px;font-style:normal;font-weight:500;font-size:14px;line-height:24px;text-align:center;color:#1a73e8;border:1px solid #bdbdbd;text-decoration:none;-webkit-appearance:none;-moz-appearance:none;height:40px;padding:8px 16px;margin-right:15px;cursor:pointer}.button:hover{background:#faf9ff;color:#187cff}

.route{padding:16px}.route__row{justify-content:space-between;display:flex;padding:10px 10px 20px;text-align:left}.route__row-booked{justify-content:space-between;display:flex;padding:10px 10px 20px;text-align:left;border-radius:8px;background:#97f1ff8c}.route__ek-img{height:40px;margin-top:5px;margin-right:20px}.route__time-container{margin-right:20px}.route__destination-container{min-width:50px;max-width:150px;margin-right:20px}.route__time{font-size:16px;font-weight:500;margin:0}.route__carrier,.route__stops{color:#70757a;margin:0}.route__destination{font-size:16px;font-weight:500;margin:0}.route__selected{color:#70757a;font-size:16px;font-weight:500;padding-right:20px}.route__hr{border:0.5px solid #e4e4e4}

.routes__grey-container{margin:50px;border:1px solid #e4e4e4;border-radius:6px}.routes__grey-container-mobile{margin:10px;border:1px solid #e4e4e4;border-radius:6px}


.App{background-color:#fff;min-width:370px}

